.questions-list {
    padding: 16px;
    max-width: 600px;
    margin: auto;
    background: #f9f9f9;
    border-radius: 8px;
  }
  
  ul {
    list-style: none;
    padding: 0;
  }
  
  li {
    padding: 10px;
    margin: 5px 0;
    background: white;
    border-radius: 4px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    word-wrap: break-word;
  }
  
  .new-question {
    background-color: #e3f2fd;
  }
  
  .question-text {
    flex-grow: 1;
    text-align: justify;
  }
  
  .question-date {
    font-size: 0.8em;
    color: gray;
  }
  